
Template.__checkName("tagline_banner");
Template["tagline_banner"] = new Template("Template.tagline_banner", (function() {
  var view = this;
  return Blaze.If(function() {
    return Spacebars.call(view.lookup("showTaglineBanner"));
  }, function() {
    return [ "\n    ", HTML.DIV({
      "class": function() {
        return [ "tagline-banner ", Spacebars.mustache(view.lookup("moduleClass")) ];
      }
    }, "\n      ", HTML.H3({
      "class": "tagline"
    }, HTML.SPAN(Blaze.View("lookup:getSetting", function() {
      return Spacebars.mustache(view.lookup("getSetting"), "tagline");
    }))), "\n    "), "\n  " ];
  });
}));
